import { Component, OnInit, OnDestroy } from '@angular/core';
import { Serie } from 'src/app/services/interfaces/serie';
import { DataSerieService } from 'src/app/services/data-serie.service';
import { NavController } from '@ionic/angular';
import { Subscription } from 'rxjs';

@Component({
	selector: 'app-selectionner-serie',
	templateUrl: './selectionner-serie.page.html',
	styleUrls: [ './selectionner-serie.page.scss' ]
})
export class SelectionnerSeriePage implements OnInit, OnDestroy {
	searchInput: string;
	series: Serie[];

	private serieSub: Subscription;

	constructor(private seriesService: DataSerieService, private NavCtrl: NavController) {
		this.series = [ ...seriesService.serieNonVu ];
	}

	ngOnInit() {
		this.serieSub = this.seriesService.serieNonVuChanged.subscribe((serie) => {
			this.series = serie;
		});
	}

	ngOnDestroy() {
		this.serieSub.unsubscribe();
	}

	goToAjouter() {
		this.NavCtrl.navigateForward([ '/selectionner-serie/ajouter-serie' ]);
	}

	goBack() {
		this.NavCtrl.navigateBack([ '/medias/series' ]);
	}

	isFinished(s: Serie) {
		return !(s.dateF === undefined);
	}

	showInfo(id: number) {
		this.NavCtrl.navigateForward([ '/info-add-serie/' + id ]);
	}
}
