import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SelectionnerSeriePage } from './selectionner-serie.page';

const routes: Routes = [
	{
		path: '',
		component: SelectionnerSeriePage
	},
	{
		path: 'ajouter-serie',
		loadChildren: () => import('./ajouter-serie/ajouter-serie.module').then((m) => m.AjouterSeriePageModule)
	}
];

@NgModule({
	imports: [ RouterModule.forChild(routes) ],
	exports: [ RouterModule ]
})
export class SelectionnerSeriePageRoutingModule {}
